import React from 'react';

function TextInput(props) {
  const inputClasses = [];
  const inputAttributes = {
    name: props.name,
    id: props.name + '-input',
    type: 'text',
    value: props.value,
  };

  props.onChange && (inputAttributes.onChange = props.onChange);
  props.validationFxn && (inputAttributes.onBlur = (event) => {
    props.validationFxn(event.target.value);
  });

  if (props.errorMessage) {
    inputClasses.push('has-error');
  }

  if (props.disabled) {
    inputClasses.push('disabled');
    inputAttributes.tabIndex = '-1';
  }

  return (
    <div className="input-wrapper">
      <label htmlFor={inputAttributes.id}>{props.label}</label>
      <input {...inputAttributes} className={inputClasses.join(' ')} />
      {props.errorMessage && <span className="input-error-msg">{props.errorMessage}</span>}
    </div>
  );
}

export default TextInput;
