import React, {Component} from 'react';

import '../scss/components/forms.scss';

import Checkbox from './inputs/Checkbox'
import Select from './inputs/Select'
import TextInput from './inputs/TextInput'

class SearchForm extends Component {
  constructor(props) {
    super(props);

    this.state = {
      text: '',
      stars: '',
      license: '',
      forked: false,
      starsErrorMessage: '',
    };

    this.handleInput = this.handleInput.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.setErrorMessage = this.setErrorMessage.bind(this);
    this.makeValidationFxn = this.makeValidationFxn.bind(this);
    this.getInitialParams = this.getInitialParams.bind(this);

    this.licenseTypes = [
      // MIT, ISC, Apache and GPL
      {
        text: 'All',
        value: ''
      },{
        text: 'ISC',
        value: 'isc'
      },{
        text: 'MIT',
        value: 'mit'
      },{
        text: 'Apache License 2.0',
        value: 'apache-2.0'
      },{
        text: 'GNU General Public License',
        value: 'gpl'
      }
    ];

    this.starsValidationFxn = this.makeValidationFxn(
      // Input name
      'stars',
      // Function for validation 
      (inputValue) => {
        // inputValue should always be a string, so don't need to check/convert

        // n | <n|>n|<=n|>=n | n..n|*..n|n..*
        const starsRegEx = /^((<|>|<=|>=)?[0-9]+|(\*|[0-9]+)\.\.(\*|[0-9]+))$/;

        // A larger number first in n..n syntax will return a 422 error (equal values ok)
        if (inputValue.match(/^([0-9]+)\.\.([0-9]+)$/)) {
          const numbers = inputValue.split('..');
          if (parseInt(numbers[0], 10) > parseInt(numbers[1], 10)) {
            return false;
          }
        }

        return inputValue.match(starsRegEx) != null;
      },
      // Error message
      // Can ignore the *..n and n..* syntaxes in this message since they're equivalent to <=n and >=n
      'Accepted formats: 50, >50, >=50, <50, <=50, 50..100 (range, n1 must be < n2).'
    ).bind(this);
  }

  componentDidMount() {
    this.getInitialParams();

    window.addEventListener('popstate', (event) => {
      this.getInitialParams();
    });
  }

  getInitialParams() {
    if (!window.location.search) {
      return;
    }

    const initialParamData = new URLSearchParams(window.location.search);

    this.setState({
      text: initialParamData.get('text') || '',
      stars: initialParamData.get('stars') || '',
      license: this.licenseTypes.map(lt => lt.value).includes(initialParamData.get('license')) ? initialParamData.get('license') : '',
      forked: initialParamData.get('forked') === "true",
    }, () => {
      const form = document.getElementById('search-form');
      const button = form && form.querySelector('button[type=submit]');
      button && button.click();
    });
  }

  handleInput(event) {
    if (this.props.loading) {
      return false;
    }

    const target = event.target;
    const name = target.name;
    let value = target.type === 'checkbox' ? target.checked : target.value;

    if (name === 'stars') {
      // Should probably show user some message about 'no spaces allowed,' but will leave for now
      value = value.replace(/\s/g, '');
    }

    this.setState({
      [name]: value
    });
  }

  handleSubmit(event) {
    event.preventDefault();

    // Disable submit if loading results
    if (this.props.loading) {
      return false;
    }

    // Validate one last time in the case of focus on invalid input,
    // change to valid, press enter to submit (no blur event)
    if (this.starsValidationFxn(this.state.stars)) {
      this.props.fetchResults(this.state);
    }
  }

  setErrorMessage(inputName, message) {
    this.setState({
      [inputName + 'ErrorMessage']: message
    });
  }

  makeValidationFxn(inputName, validationFxn, errorMessage) {
    return function(inputValue) {
      if (inputValue !== "" && !validationFxn(inputValue)) {
        this.setErrorMessage(inputName, errorMessage || "Error");
        return false;
      } else {
        this.setErrorMessage(inputName, null);
        return true;
      }
    };
  }

  render() {
    return (
      <form
        id="search-form"
        onSubmit={this.handleSubmit}
      >
        <TextInput
          label="Text"
          name="text"
          value={this.state.text}
          onChange={this.handleInput}
          disabled={this.props.loading}
        />
        <TextInput
          label="Stars"
          name="stars"
          value={this.state.stars}
          onChange={this.handleInput}
          validationFxn={this.starsValidationFxn}
          errorMessage={this.state.starsErrorMessage}
          disabled={this.props.loading}
        />
        <Select
          label="License"
          name="license"
          value={this.state.license}
          onChange={this.handleInput}
          options={this.licenseTypes}
          disabled={this.props.loading}
        />
        <Checkbox
          label="Include Forked"
          name="forked"
          checked={this.state.forked}
          onChange={this.handleInput}
          disabled={this.props.loading}
        />
        <button
          type="submit"
          className={'btn' + (this.props.loading ? ' disabled loading' : '')}
        >
          Search
        </button>
      </form>
    );
  }
}

export default SearchForm;
