import React from 'react';

function Checkbox(props) {
  const inputAttributes = {
    name: props.name,
    id: props.name + '-input',
    type: 'checkbox',
    checked: props.checked,
  };

  props.onChange && (inputAttributes.onChange = props.onChange);
  props.onBlur && (inputAttributes.onBlur = props.onBlur);

  if (props.disabled) {
    inputAttributes.className = 'disabled';
    inputAttributes.tabIndex = '-1';
  }

  return (
    <div className="input-wrapper">
      <div className="vertical-center">
        <div className="checkbox">
          <input {...inputAttributes} />
          <label htmlFor={inputAttributes.id}>{props.label}</label>
        </div>
      </div>
    </div>
  );
}

export default Checkbox;
