import React from 'react';

function Select(props) {
  const inputAttributes = {
    name: props.name,
    id: props.name + '-input',
    value: props.value,
  };

  props.onChange && (inputAttributes.onChange = props.onChange);
  props.onBlur && (inputAttributes.onBlur = props.onBlur);

  if (props.disabled) {
    inputAttributes.className = 'disabled';
    inputAttributes.tabIndex = '-1';
  }

  return (
    <div className="input-wrapper">
      <label htmlFor={inputAttributes.id}>{props.label}</label>
      <div className={'select-wrapper' + (props.disabled ? ' disabled' : '')}>
        <select {...inputAttributes}>
          {props.options.map(opt => (
            <option
              key={`opt-${opt.value}`}
              value={opt.value}
            >
              {opt.text}
            </option>
          ))}
        </select>
      </div>
    </div>
  );
}

export default Select;
