import React from 'react';

function Card(props) {
  return (
    <div className="card">
      <div className="card-section">
        {props.badge &&
          <span className="badge">{props.badge}</span>
        }
        <h5 className="title">
          {props.url ? (<a href={props.url}>{props.title}</a>) : props.title}
        </h5>
        <p className="description">{props.description}</p>
      </div>
      {props.sections.map((section) => {
        // Right now the CSS only supports two secondary .card-section elements
        return (
          <div key={section.id} className="card-section">
            <p>{section.title}: <strong>{section.value}</strong></p>
          </div>
        );
      })}
    </div>
  );
}

export default Card;
