import React, {Component} from 'react';

import '../scss/global.scss';

import CardsList from './CardsList'
import Footer from './Footer'
import Header from './Header'
import SearchForm from './SearchForm'

class RepoSearch extends Component {
  constructor(props) {
    super(props);

    this.state = {
      results: [],
      hasSearched: false,
      loading: false,
      fetchError: false,
    };

    this.fetchResults = this.fetchResults.bind(this);
  }

  fetchResults(formData) {
    // If no form data, bail
    if (!(formData.text || formData.stars || formData.license)) {
      this.setState({
        results: [],
        hasSearched: false
      });
      return;
    }

    // Disable the search UI
    this.setState({
      loading: true
    });
    document.activeElement.blur();

    // ex: https://api.github.com/search/repositories?q=tetris+language:assembly&sort=stars&order=desc
    const apiBase = 'https://api.github.com/search/repositories';

    const params = [];
    const newHistoryParams = [];

    formData.text = formData.text.trim();

    // TODO: This could probably be generalized a bit
    if (formData.text) {
      params.push(formData.text);
      newHistoryParams.push('text=' + encodeURIComponent(formData.text));
    }
    if (formData.stars) {
      params.push('stars:' + formData.stars);
      newHistoryParams.push('stars=' + encodeURIComponent(formData.stars));
    }
    if (formData.license) {
      params.push('license:' + formData.license);
      newHistoryParams.push('license=' + encodeURIComponent(formData.license));
    }
    if (formData.forked) {
      params.push('fork:true');
      newHistoryParams.push('forked=' + encodeURIComponent(formData.forked));
    }

    const paramsString = params.join('+');
    const url = apiBase + '?q=' + paramsString;

    fetch(url)
      // This method handles more errors than just network or permissions (e.g., 404)
      // https://developer.mozilla.org/en-US/docs/Web/API/Fetch_API/Using_Fetch#Checking_that_the_fetch_was_successful
      .then(response => {
        if (!response.ok) {
          throw Error(response.statusText);
        }
        return response;
      })
      .then(response => response.json())
      .then(data => {
        this.setState({
          results: data.items || [],
          loading: false,
          hasSearched: true
        });
      })
      .catch((error) => {
        // Handle fetch errors - we'll show a user friendly message below the form
        console.log(error);
        this.setState({
          results: [],
          loading: false,
          hasSearched: true,
          fetchError: true,
        });
      });

    // Push the state containing updated form data in parameters
    const newStateString = '?' + newHistoryParams.join('&');
    window.history.pushState(null, '', window.location.origin + window.location.pathname + newStateString);
  }

  render() {
    return (
      <div className="outer-wrap">
        <Header />

        <div className="container">
          <div className="section">
            <h1>Even Financial GitHub Repository Search</h1>
          </div>

          <section className="section">
            <SearchForm
              fetchResults={this.fetchResults}
              loading={this.state.loading}
            />
          </section>

          <div className="section">
            <hr />
          </div>

          <section className="section">
            <CardsList
              results={this.state.results}
              hasSearched={this.state.hasSearched}
              loading={this.state.loading}
              fetchError={this.state.fetchError}
            />
          </section>
        </div>

        <Footer />
      </div>
    );
  }
}

export default RepoSearch;
