import React from 'react';

import '../scss/components/cards.scss';

import Card from './Card'

function CardsList(props) {
  // Message to show above list
  const message = (() => {
    if (props.fetchError) {
      return (<span>There was an error in fetching your results.<br />Please try another search or wait 30 seconds and try again.</span>);
    } else if (props.loading) {
      return 'Looking for matches...';
    } else if (props.results.length) {
      return 'SEARCH results:';
    } else if (!props.hasSearched) {
      return 'Please enter query and click SEARCH button above, results appear here.';
    } else {
      return 'No results found, please try another search.';
    }
  })();

  return (
    <div>
      <p className="message">{message}</p>
      {!!props.results.length &&
        <div className="card-group">
          {props.results.map((repo) => {
            return (
              <Card key={repo.id}
                url={repo.html_url}
                title={repo.full_name}
                description={repo.description}
                badge={repo.fork ? 'forked' : null}
                sections={[
                  {
                    id: 1,
                    title: 'Stars',
                    value: repo.stargazers_count
                  },{
                    id: 2,
                    title: 'License',
                    value: repo.license && repo.license.name ? repo.license.name : '(none set)'
                  }
                ]}
              />
            );
          })}
        </div>
      }
    </div>
  );
}

export default CardsList;
